.Footer {
    width: 100%;
    height: 253px;
    background-color: #EDEDED;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.Footer .FooterLogo {
    margin-bottom: 30px;
}
.Footer .Content {
    position: relative;
    font-size: 14px;
    font-weight: 600;
    color: #B1B1B1;
    margin-bottom: 20px;
}
.Footer .Content::after {
    content: "";
    position: absolute;
    left: 0px;
    bottom: -10px;
    height: 1px;
    width: 100%;
    background: #B1B1B1;
}
.Footer .Copyright {
    color: #B1B1B1;
    font-size: 14px;
    font-weight: 800;
    margin-bottom: 10px;
}
.Footer .Email {
    color: #B1B1B1;
    font-size: 12px;
    font-weight: 600;
}