@media (max-width : 768px) {
    .Main .ProductTitleWrap {
        padding: 0 30px;
        flex-direction: column;
        align-items: center;
    }
    .Main .ProductWrap {
        flex-wrap: wrap;
        align-items: flex-end;
    }

    .Main .ProductItem {
        width: 50%;
        margin-bottom: 20px;
        border: none;
    }
    .Main .ProductItem .text-3xl {
        font-size: 24px;
        margin-bottom: 0px !important;
    }
    .Main .ProductItem .text-base {
        font-size: 13px;
    }
    .Main .ProductItem .text-sm {
        display: none;
    }
    .Main .ProductItem .ProductImgWrap {
        min-height: 170px;
        margin-bottom: 10px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: flex-end;
    }
    .ProductTitle {
        width: 100%;
        padding-left: 0;
        text-align: left;
        margin-bottom: 10px;
    }
    .ProductSubTitle {
        width: 100%;
        text-align: left;
        position: relative;
    }
    .ProductSubTitle::after {
        content: "";
        position: absolute;
        left: 0px;
        bottom: -20px;
        height: 1px;
        width: 100%;
        background: #DADADA;
    }
    .text-3xl {
        font-size: 20px;
        line-height: 2.25rem;
    }
    .text-base {
        font-size: 14px;
    }
    /* .Main .Model30 img {
        width: 95%;
    } */
    .ProductTitle02 {
        width: 100%;
        padding-left: 0px;
        margin-right: 0px;
        margin-bottom: 0px;
    }
    .ProductTitle02 .text-4xl {
        font-size: 30px;    
        text-align: left;
        margin-bottom: 0px;
    }
    .Main .Product {
        padding: 30px 0;
    }
}