.EventBanner {
    width: 100%;
    padding: 15px;
    text-align: center;
    background-color: #5A0101;
}
.EventBanner p {
    font-size: 16px;
    font-weight: 800;
    color: #FFF;
}
.EventBanner span {
    margin-left: 20px;
    font-size: 16px;
    font-weight: 400;
    color: #E3CA98;
}
@media (max-width : 768px) {
    .EventBanner p {
        font-size: 13px;
        font-weight: 800;
        color: #FFF;
    }
    .EventBanner span {
        margin-left: 20px;
        font-size: 13px;
        font-weight: 400;
        color: #E3CA98;
    }
}