.Contact {
    padding: 80px 0;
}
.Contact .Wrap h2 {
    font-size: 32px;
    font-weight: 600;
    text-align: center;
    margin-bottom: 80px;
}
.Contact .Wrap .ContactImg {
    max-width: 95%;
    margin: 0 auto 80px;
}
.Contact .Wrap .Left {
    width: 50%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}
.Contact input {
    padding: 10px 20px;
    border: 1px solid #777;
    border-radius: 5px;
    background-color: #EDEDED;
    margin-bottom: 40px;
}
.Contact textarea {
    padding: 10px 20px;
    border: 1px solid #777;
    border-radius: 5px;
    background-color: #EDEDED;
    margin-bottom: 40px;
}
.SendBtn {
    width: 100%;
    font-size: 20px;
    color: #FFF;
    padding: 15px 0;
    border-radius: 10px;
    background-color: #EC0000;
}
@media (max-width : 768px) {
    .Contact .Wrap .Left {
        width: 100%;
    }
}