.Banner {
    width: 100%;
    height: 583px;
    background-image: url('../../assets/images/MainBG.png');
}
.BannerWrap {
    width: 100%;
    max-width: 1040px;
    height: 100%;
    padding: 0 20px;
    margin: 0 auto;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}
.Banner .SelectWrap {
    cursor: pointer;
    display: flex;
    align-items: center;
    flex-direction: row;
}
@media (max-width : 768px) {
    .Banner {
        background-image: url('../../assets/images/MobileBG.png');
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
    .BannerWrap {
        padding: 0 50px;
    }
    .BannerWrap .text-3xl {
        font-size: 30px;
        font-weight: 600;
    }
}