.Sending {
    position: fixed;
    display: flex;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 999;
    background-color: rgba(0, 0, 0, 0.35);
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.Sending h2 {
    font-size: 84px;
    font-weight: 600;
    color: #FFF;
}