.Sidebar {
    display: none;
}
@media ( max-width : 768px ) {
    .Sidebar {
        position: fixed;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        width: 300px;
        height: 100vh;
        padding: 80px 20px 30px 40px;
        background-color: #FFF;
        margin-left: -300px;
        transition: all 0.3;
        box-sizing: border-box;
        font-size: 20px;
        font-weight: 600;
    }
    .Sidebar a, .Sidebar p {
        margin-bottom: 20px;
    }
    .subMenu {
        font-size: 16px;
        margin-left: 5px;
    }
    .subMenu02 {
        font-size: 16px;
        margin-left: 15px;
    }

    .Sidebar .Logo{
        margin-bottom: 50px;
    }
    .Sidebar .ContactBtn{
        /* margin-top: 50px; */
    }
    .SidebarActive {
        margin-left: 0px;
        z-index: 2;
        transition: all 0.3s;
    }
    .Outside {
        position: fixed;
        width: 100%;
        height: 100vh;
        z-index: -999;
        background-color: rgba(0, 0, 0, 0.65);
    }
    .OutsideActive {
        z-index: 1;
    }
    .Sidebar .AMPLIFIERS {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    .Sidebar .MODEL30 {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    .Sidebar .CABINETS {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
}